
import CorpoHumanoInterativo from './CorpoHumanoInterativo';
import React, { useState, useRef } from 'react';
import { useRouter } from 'next/router';
import { FiUploadCloud } from 'react-icons/fi';

const CardAvaliacaoInicial = ({ onComplete }) => {

  const [exames, setExames] = useState([]);
  const [temDor, setTemDor] = useState(null);
  const [examePreview, setExamePreview] = useState([]);
  const [erro, setErro] = useState('');
  const [enviado, setEnviado] = useState(false);
  const [regioesDor, setRegioesDor] = useState([]);
  const [dragActive, setDragActive] = useState(false);
  const fileInputRef = useRef(null);
  const router = useRouter();

  const handleExameChange = (e) => {
    const files = Array.from(e.target.files);
    setExames(files);
    setExamePreview(files.map(file => URL.createObjectURL(file)));
  };

  const handleDrop = (e) => {
    e.preventDefault();
    setDragActive(false);
    const files = Array.from(e.dataTransfer.files);
    setExames(files);
    setExamePreview(files.map(file => URL.createObjectURL(file)));
  };

  const handleDragOver = (e) => {
    e.preventDefault();
    setDragActive(true);
  };

  const handleDragLeave = (e) => {
    e.preventDefault();
    setDragActive(false);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (temDor === null) {
      setErro('Por favor, responda se voc√™ sente dor.');
      return;
    }
    if (exames.length === 0) {
      setErro('Por favor, fa√ßa upload de pelo menos um exame ou documento.');
      return;
    }
    if (temDor && regioesDor.length === 0) {
      setErro('Por favor, marque no corpo onde sente dor.');
      return;
    }
    setErro('');
    setEnviado(true);
    if (onComplete) onComplete();
  };

  if (enviado) {
    return (
      <div className="flex flex-col items-center justify-center min-h-[300px]">
        <div className="rounded-lg shadow-md bg-green-50 p-6 text-center max-w-md w-full">
          <h3 className="text-lg font-semibold mb-2">Avalia√ß√£o enviada!</h3>
          <p className="text-green-700">Obrigado por completar sua avalia√ß√£o inicial.</p>
        </div>
      </div>
    );
  }

  return (
    <div className="flex flex-col items-center justify-center min-h-[500px] bg-gray-100 py-8">
      <form onSubmit={handleSubmit} className="rounded-2xl shadow-xl bg-white p-8 w-full max-w-lg flex flex-col items-center">
        <h3 className="text-3xl font-bold mb-8 text-center text-gray-800">Avalia√ß√£o Inicial</h3>
        {/* Upload moderno */}
        <div className="mb-8 w-full">
          <label className="block font-medium mb-3 text-center text-gray-700">Upload de exames/documentos</label>
          <div
            className={`flex flex-col items-center justify-center border-2 border-dashed rounded-xl p-6 cursor-pointer transition-colors duration-200 ${dragActive ? 'border-blue-400 bg-blue-50' : 'border-blue-200 bg-gray-50'}`}
            onClick={() => fileInputRef.current && fileInputRef.current.click()}
            onDrop={handleDrop}
            onDragOver={handleDragOver}
            onDragLeave={handleDragLeave}
          >
            <FiUploadCloud className="text-4xl text-blue-400 mb-2" />
            <span className="text-gray-500 mb-2">Arraste seus exames aqui ou clique para selecionar</span>
            <input
              ref={fileInputRef}
              type="file"
              accept="image/*,application/pdf"
              multiple
              onChange={handleExameChange}
              className="hidden"
            />
            <div className="flex flex-wrap mt-2 gap-2 justify-center">
              {examePreview.map((src, idx) => (
                <img key={idx} src={src} alt="Pr√©via" className="w-16 h-16 object-cover rounded border" />
              ))}
            </div>
          </div>
        </div>
        {/* Pergunta de dor com cards interativos */}
        <div className="mb-8 w-full flex flex-col items-center">
          <label className="block font-medium mb-3 text-center text-gray-700 text-lg">Voc√™ est√° sentindo dor hoje?</label>
          <div className="flex gap-8 justify-center mb-2 w-full">
            <button
              type="button"
              className={`flex-1 flex flex-col items-center justify-center px-0 py-4 rounded-2xl text-xl font-semibold border-2 transition-all duration-200 shadow-sm ${temDor === true ? 'bg-red-100 border-red-400 text-red-700 scale-105' : 'bg-gray-100 border-gray-300 text-gray-500 hover:border-red-300'}`}
              onClick={() => setTemDor(true)}
            >
              <span className="text-4xl mb-1">üòî</span>
              Sim
            </button>
            <button
              type="button"
              className={`flex-1 flex flex-col items-center justify-center px-0 py-4 rounded-2xl text-xl font-semibold border-2 transition-all duration-200 shadow-sm ${temDor === false ? 'bg-green-100 border-green-400 text-green-700 scale-105' : 'bg-gray-100 border-gray-300 text-gray-500 hover:border-green-300'}`}
              onClick={() => setTemDor(false)}
            >
              <span className="text-4xl mb-1">üòÄ</span>
              N√£o
            </button>
          </div>
          {temDor === true && (
            <div className="w-full mt-4">
              <CorpoHumanoInterativo value={regioesDor} onChange={setRegioesDor} />
            </div>
          )}
          {temDor === false && (
            <div className="text-gray-600 text-center mt-4">√ìtimo! Caso sinta dor futuramente, vamos tratar juntos.</div>
          )}
        </div>
        {erro && <div className="text-red-600 mb-4 text-center">{erro}</div>}
        <button
          type="submit"
          className="w-full bg-blue-600 text-white py-4 rounded-xl text-xl font-bold hover:bg-blue-700 transition mt-2 shadow-lg"
        >
          Enviar avalia√ß√£o
        </button>
      </form>
    </div>
  );
};

export default CardAvaliacaoInicial;
